body, html{
    height: 100%;
    #root{
        height: 100%;
    }
    font-size: 13px;
}

h1{
    font-size: 16px;
}

.myNav{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
    .logo{
        a{
            text-decoration: none;
            color: #ddd;
            h2{
                font-size: 20px;
                line-height: 60px;
            }
        }
    }
    .myContainer{
        position: relative;
        background-color: #ffffff00;
        padding: 0px 50px 0 50px;
        display: flex;
        justify-content: space-between;
        
        .navUL {
            margin: 20px 0 0 0;
            padding: 0;
            list-style: none;
            display: flex;
            justify-content: space-between;
            width: 250px;
            li { 
                display: inline-block; 
            }
            
            a {
                padding: 7px 15px;
                display: block;
                text-decoration: none;
                border-radius: 20px;
                color: #fff;
                text-shadow: 4em;
                font-weight: 400;
            }
        }
        
        

        .active{
            background-color: grey;
        }
    }

    
}

.legendContent{
    width: 100%;
    position: relative;
    letter-spacing: 0.012em;
    .buttonWrap{
        margin: 80px 0 0 50px;
        position: relative;
        .legendButtons{
            width: 200px;
            a{text-decoration: none; }
            .contactBtn{
                width: 200px;
                height: 40px;
                line-height: 40px;
            }
            a .projectBtn{
                width: 100px;
                height: 30px;
                line-height: 30px;
            }
            .legendButton{
                border-radius: 20px;
                font-size: 16px;
                letter-spacing: 1px;
                margin: 5px 0;
                box-shadow: 2px 5px 20px 7px rgba(0,0,0,0.12), -1px -2px 10px 2px rgba(0,0,0,0.11);
                cursor: pointer;
                text-align: center;
                &:active{
                    box-shadow: none;
                }
            }
        }
    }
    

    .contactBtn{
        background: rgb(65, 182, 65);
        color: #fff;
    }

    .projectBtn{
        background: rgb(148, 147, 147);
        color: #fff;
    }

   

    .legendCards{
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
        margin: 50px 0 50px 0;
        .myCard{
            margin-top: 20px;
            padding: 10px;
            position: relative;
            width: 500px;
            background: #fff;
            border-radius: 3px;
            box-shadow: 2px 5px 20px 7px rgba(0,0,0,0.25), -1px -2px 10px 2px rgba(0,0,0,0.22);
            transition: 0.4s;
            .cardHeader{
                text-align: center;
                font-size: 13px;
                h2{
                    font-size: 12px;
                    padding-top: 2px;
                    font-weight: 500;
                }
            }

            .cardBody{
                height: 200px;
                position: relative;
                .cardContent{
                    height: 100%;
                    width: 100%;
                }
            }
        }

       
    }
}

.homeContainer{
    position: relative;
    //height: 100%;
    display: flex;
    .home{
        display: flex;
        position: relative;
        height: 100%;
        z-index: 0;
        width: 100%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-image: url("./assets/1.svg");
        .homeCover{
            position: absolute;
            height: 100%;
            display: flex;
            flex: 1 0 auto;
            background-color: rgb(48, 47, 47);
            width: 100%;
            top: 0;
            opacity: .7;
            z-index: 0;
        }
    }
}

.page{
    position: relative;
    padding: 30px 70px;
    text-align: center;
    background: #ddd;
    border-radius: 5px 5px 0 0;
  
    .pageTitle{
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
    }

    h1{
        font-size: 1.5rem;
        padding: 20px 10px 0 10px;
    }
}

.projects{
    background: #fff;
    border-radius: 5px 5px 0 0;
    margin: 120px 50px 0 50px;
    padding: 10px 10px 30px 10px;
    box-shadow: 0px 0px 20px 7px rgba(0,0,0,0.12), 0px 0px 0px 0px rgba(0,0,0,0.11);

    h1{
        margin-left: 0px; 
    }
   
    .nav-pills{
        margin-top: 20px;
        .nav-item a{
            color: #000;
            &.active{
                color: #fff;
                background: #41b641;
            }
        }
    }    
}

//Menu Icon styles
.menuIcon{
    margin:20px 0px 0 0;
    cursor: pointer;
}

.bar1, .bar2, .bar3 {
    width: 30px;
    height: 3px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.4s;
}

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-4px, 2px);
    transform: rotate(-45deg) translate(-5px, 2px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-9px, -8px);
    transform: rotate(45deg) translate(-9px, -8px);
}

.slideMenu{
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    background: #f4f4f4;
    text-align: center;
    transition: .1s;
    z-index: 1;
    ul{
        position: relative;
        height: 100%;
        list-style: none;
        text-decoration: none;
        padding: 0px 0 0px 0;
        margin: 0;
        cursor: pointer;
        li{
            position: relative;
            &:hover{
                border-bottom: #41b641 solid 1px;
            }

            &:active{
                background: rgb(236, 235, 235);
            }

            a{
                text-decoration: none;
                color: #000;
            }

            div{
                height: 50px;
                line-height: 50px;
                width: 100%;
            }
        }
    }
}

.footer{
    padding: 30px 70px;
    background: #000;
    color: #ddd;
    position: relative;
    i{
        font-size: 25px;
        padding: 7px 0;
    }

    .fa-envelope{
        font-size: 16px;
    }
    a{
        text-decoration: none;
        color: #ddd;
    }

    .copyright{
        width: 100%;
        text-align: center;
        margin-top: 30px;
    }
    .useful-links{
        position: absolute;
        right: 80px;
        top: 30px;
    }
    h2{
        font-size: 16px;
        margin-bottom: 2px; 
    }
}

@media only screen and (min-width: 1440px){
    .legendContent{
        .legendCards{
            .myCard{
                width: 500px;
            }
        }
    }

    .homeContainer{
        height: 100%;
    }
}

@media only screen and (max-width: 1440px){
    .legendContent{
        .legendCards{
            .myCard{
                width: 350px;
            }
            margin-left: 50px;
            margin-right: 50px;
        }
    }

    .homeContainer{
        height: 100%;
    }
}

@media only screen and (max-width: 1024px){
    .legendContent{
       
        .legendCards{
            
            margin-top: 50px;
            .myCard{
                width: 250px;
                .cardBody{
                    height: 230px;
                }
            }
        }
    }

    .homeContainer{
        height: auto;
    }
}

@media only screen and (max-width: 768px){
    .myNav{
        .myContainer{
            padding: 0 10px 0 10px;
        }
    }

    .legendContent{
        .buttonWrap{
            margin-left: 10px;
        }
        .legendCards{
            margin-left: 10px;
            margin-right: 10px;
            .myCard{
                width: 600px;
            }
        }
    }

    .homeContainer{
        height: auto;
    }

    .footer{
        padding: 30px 20px 5px 20px;
        .useful-links{
            position: relative;
            top: 10px;
            right: 0
        }
    }

    .menuIcon{
        margin:20px 0px 0 0;
    }

    .projects{
        margin: 100px 10px 0 10px;
    }
}

@media only screen and (max-width: 425px){

    .menuIcon{
        margin:20px 0px 0 0;
    }

    
}
